.content {
  .featured {
    position: relative;
    margin: 50px auto;

    hr {
      width: 100%;
      margin: 5px 0;
      background: $featured-border; }

    .img-wrap {
      height: 500px;
      overflow: hidden;
      position: relative;

      img {
        position: absolute;
        top: 50%;
        min-height: 100%;
        display: block;
        left: 50%;
        @include transform(translate(-50%, -50%));
        min-width: 100%; } }

    .featured-post-wrap {
      margin: 0 auto;
      @include position(absolute, null 0 60px 0);
      width: 70%;
      color: $featured-text;

      h2 {
        font: {
          family: $blog-font;
          size: 48px; }
        margin: 0; }

      p {
        text-align: justify;
        font: {
          family: $primary-font;
          size: 24px;
          weight: 300; }
        margin: 0 0 30px; }

      .read-more {
        color: $featured-text;
        background: transparentize($read-more, 0.1);
        text-transform: uppercase;
        text-decoration: none;
        font: {
          family: $primary-font;
          size: 24px;
          weight: 300; }
        border-radius: 0px;
        padding: 10px 30px;

        &:hover {
          background: $read-more; }

        &:active {
          background: transparentize($read-more, 0.1); } } } }

  .posts-wrap {
    margin: 100px auto 0;
    width: 80%;
    box-shadow: 0 0 3px 1px $navbar-shadow;

    .posts {
      max-width: 100%;
      @include display(flex);
      @include flex-wrap(wrap);
      @include justify-content(space-around);
      @include align-items(flex-start);

      .last-post {
        width: 100%;
        padding: 20px;

        .last-post-img {
          position: relative;
          height: 400px;
          overflow: hidden;
          margin-bottom: 10px;

          img {
            position: absolute;
            top: 50%;
            min-height: 100%;
            display: block;
            left: 50%;
            @include transform(translate(-50%, -50%));
            min-width: 100%; } } }

      .last-post, .post {
        p.date {
          font: {
            family: $primary-font;
            size: 12px;
            weight: 700; }
          text-transform: uppercase;
          color: $post-paragraph;
          margin: 0; }

        h2 {
          font: {
            family: $blog-font;
            size: 42px; }
          margin: 10px 0;
          color: $post-title; }

        p {
          text-align: justify;
          font: {
            family: $primary-font;
            size: 20px;
            weight: 300; }
          margin: 0;
          color: $post-paragraph; }

        ul {
          list-style-type: none;
          padding: 0;
          margin-top: 25px;

          a {
            margin: 0 20px 0 0;
            padding: 0; }

          li {
            text-transform: uppercase;
            font: {
              family: $primary-font;
              size: 12px;
              weight: 700; }
            display: inline-block;
            padding: 0;
            margin: 0; } } }

      .post {
        margin: 20px 0 30px;
        width: 500px;

        .post-img {
          height: 400px;
          overflow: hidden;
          position: relative;

          img {
            position: absolute;
            top: 50%;
            min-height: 100%;
            display: block;
            left: 50%;
            @include transform(translate(-50%, -50%));
            min-width: 100%; } }

        .post-info {
          padding: 10px;
          border-left: 1px solid $hr-color;
          border-right: 1px solid $hr-color;
          border-bottom: 1px solid $hr-color; } } } } }

#content {
  .featured {
    background: $featured;
    padding: 50px 100px;
    margin: 20px 50px 50px;
    text-align: justify;
    border-radius: 5px;

    h1 {
      margin: 0 0 20px;
      font: {
        size: 60px;
        family: $primary-font; } }

    p {
      font: {
        family: $primary-font; } } }

  .posts-wrap {
    margin: 0 0 100px;
    .post {
      margin: 30px 50px;
      text-align: justify;

      h1 {
        margin: 0 0 15px;
        font: {
          size: 48px; }
        color: $heading; }

      p {
        color: $paragraph; }

      .criado-por {
        font-size: 14px;
        text-align: left; } } }

  .sidebar {
    margin: 30px 0 0;
    border-left: 1px solid $vr-color;
    padding: 20px 30px;

    .most-recent, .categories {
      margin: 20px 0 50px; }

    dl {
      dt {
        color: $heading;
        font-weight: normal; }
      dd {
        color: {} } } }

  .margin-top-20 {
    margin-top: 20px; }

  .edit-form {
    margin: 35px; } }

#create {
  .form-wrap {
    .btn {
      margin: 15px 0 100px; } } }
